import React from "react";
import IconButton from "@material-ui/core/IconButton";
import InputLabel from "@material-ui/core/InputLabel";
import Visibility from "@material-ui/icons/Visibility";
import InputAdornment from "@material-ui/core/InputAdornment";
import VisibilityOff from "@material-ui/icons/VisibilityOff";
import Input from "@material-ui/core/Input";
export default function Nav() {
    const [values, setValues] = React.useState({
        password: "",
        showPassword: false,
    });

    const handleClickShowPassword = () => {
        setValues({ ...values, showPassword: !values.showPassword });
    };

    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };

    const handlePasswordChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
    };

    return (
        <div>
            <nav class="navbar navbar-expand-lg navbar-light ">

                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>

                <div class="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul class="navbar-nav mr-auto">
                    </ul>
                    <div class=" my-2 my-lg-0">
                        <li class="nav-item">
                            <a class="nav-link text-white" data-toggle="modal" data-target="#exampleModal" >Sign In</a>
                        </li>
                    </div>
                </div>
            </nav>





            <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog" role="document">
                    <div class="modal-content">
                       
                        <div className="container mt-4">

                        <button type="button" class="close text-white" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true" className="text-white">&times;</span>
                            </button>
                        <h6 class="modal-title text-white" id="exampleModalLabel">Sign in to Continue</h6>
                        </div>
                        <div class="text-white modal-body mt-4">
                            <form>
                                
                                <div className="form-group">
                                    <InputLabel htmlFor="standard-adornment-password" className="text-white">
                                        Email ID:
                                    </InputLabel>
                                    <Input
                                        type="email"
                                        value={values.email} className="text-white inputline" required/>
                                </div>
                                <div className="form-group">
                                    <InputLabel htmlFor="standard-adornment-password" className="text-white">
                                        Password
                                    </InputLabel>
                                    <Input
                                       type="password"
                                        required
                                        className=" text-white inputline"
                                        onChange={handlePasswordChange("password")}
                                        value={values.password}
                                        endAdornment={
                                            <InputAdornment position="end" >
                                                {/* <IconButton
                                                    onClick={handleClickShowPassword}
                                                    onMouseDown={handleMouseDownPassword} className="text-white" >
                                                    {values.showPassword ? <Visibility /> : <VisibilityOff />}
                                                </IconButton> */}
                                            </InputAdornment>
                                        } />
                                </div>

                                <div className="float-right">
                                    <button className="btn btn-primary submitbtn" type="submit" ><p className="signin">Sign in</p></button>
                                </div>
                            </form>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    )

}
